.footer {
  display: flex;
  width: 100%;
  flex-direction: column;
  background-image: url(../../assets//images//background.png);
  background-position: center;
  background-size: cover;
  margin-top: 24px;

  &__img-container {
    img {
      width: 100%;
      height: 150px;
      object-fit: cover;
      object-position: 100% 20%;
    }
  }

  &__footer-container {
    width: 100%;
    height: 240px;

    padding: 0;
    margin: 0;
  }
}

@media (min-width: 819px) {
  .footer {
    &__img-container {
      img {
        width: 100%;
        height: 250px;
        object-fit: cover;
        object-position: 100% 20%;
      }
    }

    &__footer-container {
      width: 100%;
      height: 350px;
    }
  }
}

@media (min-width: 1199px) {
  .footer {
    &__img-container {
      img {
        width: 100%;
        height: 300px;
        object-fit: cover;
        object-position: 100% 20%;
      }
    }

    &__footer-container {
      width: 100%;
      height: 350px;
    }
  }
}
