.service-personal {
  width: 100%;
  margin-top: 42px;
  display: flex;
  flex-direction: column;
  align-items: center;

  &__pic-wrapper {
    // width: 100%;
    // height: 220px;
    img {
      width: 100%;
      height: 220px;
      object-fit: cover;
    }
  }

  &__cont-wrapper {
    h1 {
      margin-top: 24px;
      color: #fff;
      font-size: 24px;
      text-align: center;
    }
  }
}

@media (min-width: 799px) {
  .service-personal {
    &__pic-wrapper {
      // width: 100%;
      height: 300px;
      img {
        width: 100%;
        height: 300px;
        object-fit: cover;
      }
    }
  }
}
